/**
 * RetirementDetailContainer.js
 * The detail container for retirement
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { getFormValues } from 'redux-form';
import PropTypes from 'prop-types';

// Import Insurer Logos
import Logos from 'assets/images/InsurerLogos';

import RetirementDetail from '../../components/Retirement/RetirementDetail';
import { setInsuranceFormState, getAnonymousSession, FORMSTATES } from '../../actions/insuranceActions';
import { getSessionParams } from '../../actions/retirementActions';
import { fbTag, gaTag, gaEvent, ampTag } from '../../lib/tagFunctions';

class RetirementDetailContainer extends Component {
  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.DETAILFORM.index);
    fbTag('PPS2 - Pageview - Retirement - DetailForm', { Product: 'Retirement' });
    gaTag({
      page_title: 'Retirement - Detail Form',
      page_path: '/retirement/detailform',
    });
    ampTag('RetireDetailsS');
  }

  render() {
    const {
      plansData,
      isLoggedIn,
      profileValues,
      piggyCallback,
      retirementFields,
      checkOutOthers,
      sessionId,
      history,
    } = this.props;

    if (!plansData) {
      return null;
    }

    const selectedData = plansData;

    let planDetails = {
      logo: Logos.others,
      logoDesc: '',
      planName: '',
      planTotal: null,
    };

    try {
      planDetails = {
        logo: Logos[selectedData.info.insurer_slug],
        logoDesc: `${selectedData.info.insurer_name}.svg`,
        planName: selectedData.info.product_name,
        planTotal: `$${selectedData.policyInfo[selectedData.planType].net_price.toFixed(2)}`,
      };
    } catch (error) {
      console.log('Get plan details failed');
    }

    return (
      <RetirementDetail
        checkOutOthers={checkOutOthers}
        retirementFields={retirementFields}
        sessionId={sessionId}
        selectedData={selectedData}
        planDetails={planDetails}
        isLoggedIn={isLoggedIn}
        piggyCallback={piggyCallback}
        changePlan={() => {
          history.goBack();
          fbTag('PPS2 - ButtonClick - Retirement - DetailForm_ChangePlan', { Product: 'Retirement', Click_Text: 'ChangePlan' });
          gaEvent('Retirement - DetailForm - ChangePlan', {
            event_category: 'Retirement',
            event_label: 'Retirement - DetailForm - ChangePlan',
          });
        }}
        profileValues={profileValues}
        // submitCallback={() => { setFormState(FORMSTATES.CHECKOUT); }}
      />
    );
  }
}

RetirementDetailContainer.defaultProps = {
  sessionId: null,
  plansData: null,
};

RetirementDetailContainer.propTypes = {
  plansData: PropTypes.object,
  piggyCallback: PropTypes.func.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  setFormState: PropTypes.func.isRequired,
  profileValues: PropTypes.object.isRequired,
  retirementFields: PropTypes.object.isRequired,
  checkOutOthers: PropTypes.func.isRequired,
  sessionId: PropTypes.string,
  history: PropTypes.object.isRequired,
};


const mapStateToProps = state => ({
  retirementForm: getFormValues('retirement')(state),
  retirementFields: state.retirement.retirementFields,
  profileValues: state.userDetails,
  plansData: state.retirement.selectedPlanData,
  sessionId: state.insurance.sessionId,
});

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  getAnonymousSession: (params) => {
    dispatch(getAnonymousSession(params, true));
  },
});

const mergeProps = (propsFromState, propsFromDispatch, ownProps) => ({
  ...ownProps,
  ...propsFromState,
  ...propsFromDispatch,
  checkOutOthers: (detailForm) => {
    propsFromDispatch.getAnonymousSession(getSessionParams(
      propsFromState.retirementForm,
      detailForm,
      propsFromState.plansData,
    ), true);
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps, mergeProps)(RetirementDetailContainer));
