/**
 * RetirementDetail.js
 * Detail view for Retirement
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import document from 'global/document';
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { reduxForm, getFormValues } from 'redux-form';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

// Import Insurer Logos
import DetailForm from '../Templates/DetailForm/DetailForm';

import TextInput from '../Forms/TextInput/TextInput';
import DatePickerV2 from '../Forms/DatePicker/DatePickerV2';
import CheckoutComplete from '../Templates/CheckoutPopup/CheckoutComplete';
import PopupWithText from '../Templates/PopupWithText/PopupWithText';

import { getSynergyJSX } from '../../actions/insuranceActions';
import { validateForm, dateReValidate } from '../../lib/validators';
import { setLoading, clearLoading } from '../../actions/loadingScreenActions';
import { fbTag, gaEvent, ampTag } from '../../lib/tagFunctions';

const formName = 'retirement-detail';

class RetirementDetail extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const {
      sessionId,
    } = nextProps;

    if (sessionId !== prevState.sessionId) {
      return ({
        sessionId,
      });
    }

    return null;
  }

  constructor(props) {
    super(props);

    this.state = {
      sessionId: null,
      errorStringData: {},
      errorArray: [],
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.validateForm = this.validateForm.bind(this);
  }

  componentDidUpdate() {
    const { sessionId } = this.state;
    const { loadingEnd } = this.props;

    if (sessionId) {
      loadingEnd();
      setTimeout(() => {
        this.checkoutComplete.openCheckoutComplete();
        ampTag('RetireConfirmationS');
      }, 300);
    }
  }

  validateForm(e) {
    const fieldsToCheck = [
      'email',
      'name',
      'birth_date',
      'mobile_number',
    ];

    const error = validateForm(e, fieldsToCheck);

    this.setState({
      errorStringData: error,
    });

    let errorArray = [];
    if (!_.isEmpty(error)) {
      errorArray = [<p className="error-string" key="error-1">Fields are not properly filled, please check again.</p>];
    }

    this.setState({
      errorArray,
    });

    return error;
  }

  handleSubmit(e, isFirstValidate = true) {
    // console.log(e);
    const { checkOutOthers, loading, storeForm } = this.props;
    const errorResult = this.validateForm(isFirstValidate ? e : storeForm);
    const isEmptyResult = _.isEmpty(errorResult);

    if (dateReValidate(
      isEmptyResult,
      isFirstValidate,
      errorResult,
      error => this.setState({ errorStringData: error, errorArray: null }),
      () => this.handleSubmit(storeForm, false),
    )) {
      return;
    }

    if (isEmptyResult) {
      ampTag('RetireConfirm1A');
      ampTag('RetireNoticeS');
      this.popup.openPopupWithJSX(getSynergyJSX(), true, () => {
        setTimeout(() => {
          loading();
          checkOutOthers(e);

          fbTag('PPS2 - ButtonClick - Retirement - DetailForm_Checkout', { Product: 'Retirement', Click_Text: 'Checkout' });
          gaEvent('Retirement - DetailForm - Checkout', {
            event_category: 'Retirement',
            event_label: 'Retirement - DetailForm - Checkout',
          });
          ampTag('RetireConfirm2A');
        }, 350);
      });
    }
    window.scrollTo(0, document.body.scrollHeight);
  }

  render() {
    const { errorStringData, errorArray } = this.state;
    const {
      form,
      change,
      planDetails,
      isLoggedIn,
      piggyCallback,
      changePlan,
      storeForm,
    } = this.props;

    // console.log(petMinDate, petMaxDate);
    return (
      <Fragment>
        <DetailForm
          category="retirement"
          changePlan={changePlan}
          handleSubmit={this.props.handleSubmit}
          onSubmit={this.handleSubmit}
          change={change}
          form={form}
          planDetails={planDetails}
          isLoggedIn={isLoggedIn}
          piggyCallback={piggyCallback}
          errorArray={errorArray}
          checkoutButtonDesc="Confirm"
        >
          <Row className="main-form" noGutters>
            <Col xs={12}>
              <TextInput
                form={form}
                change={change}
                name="email"
                placeholderString=""
                size="medium"
                zIndex={99}
                desc="Email Address"
                errorString={errorStringData.email}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <TextInput
                form={form}
                change={change}
                name="name"
                placeholderString=""
                size="medium"
                zIndex={98}
                desc="Full Name"
                errorString={errorStringData.name}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
            <Col className="left-padding" xs={12} sm={6}>
              <DatePickerV2
                form={form}
                change={change}
                name="birth_date"
                size="medium"
                zIndex={97}
                desc="Date of Birth"
                errorString={errorStringData.birth_date}
                validateForm={() => this.validateForm(storeForm)}
                endDate={new Date()}
              />
            </Col>
            <Col className="right-padding" xs={12} sm={6}>
              <TextInput
                form={form}
                change={change}
                name="mobile_number"
                placeholderString=""
                size="medium"
                zIndex={96}
                desc="Mobile Number"
                errorString={errorStringData.mobile_number}
                validateForm={() => this.validateForm(storeForm)}
              />
            </Col>
          </Row>
        </DetailForm>
        <PopupWithText
          ref={node => this.popup = node}
          showCancelButton
        />
        <CheckoutComplete
          isQuoteOnly
          isSellLeads
          wrappedComponentRef={node => this.checkoutComplete = node}
        />
      </Fragment>
    );
  }
}

RetirementDetail.defaultProps = {
  storeForm: null,
};

RetirementDetail.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  checkOutOthers: PropTypes.func.isRequired,
  form: PropTypes.string.isRequired,
  change: PropTypes.func.isRequired,
  planDetails: PropTypes.object.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  piggyCallback: PropTypes.func.isRequired,
  changePlan: PropTypes.func.isRequired,
  storeForm: PropTypes.object,
  loading: PropTypes.func.isRequired,
  loadingEnd: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  storeForm: getFormValues(formName)(state),
  constants: state.insurance.constants,
});

const mapDispatchToProps = dispatch => ({
  loading: (callBack, errorMsg, time) => {
    dispatch(setLoading(callBack, errorMsg, time));
  },
  loadingEnd: () => {
    dispatch(clearLoading());
  },
});

export default reduxForm({
  form: formName,
  destroyOnUnmount: false,
})(connect(mapStateToProps, mapDispatchToProps)(RetirementDetail));
